import uniNumberBox from '@/components/uni-number-box.vue';
import http from '@/components/utils/http.js';
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			server:this.apiServer,
			logo:this.apiServer+"/img/gls_logo.png",
			psid: 0,
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			cartList: [],
			freight:0.00,
			total: 0, //总价格
			addressData: {
				
			}
		};
	},
	onLoad(option) {
		
		//加载默认地址
		let opts = {
			url: '/addressApi/getdefaultaddress/',
			method: 'post'
		};
		let param = {};
		http.httpTokenRequest(opts, param).then(
			res => {
				//打印请求返回的数据
				if (res.data['code'] == 0) {
				this.addressData=res.data['defaultaddress'];
				} else {
					uni.showToast({ title: res.data.msg, icon: 'none' });
				}
			},
			error => {
				console.log(error);
			}
		);
		
		
		//商品数据
		if (option.psid != undefined) {
			this.psid=option.psid ;
			let opts = {
				url: '/productApi/createorderbypsid/',
				method: 'post'
			};
			
			let param = {psid:option.psid,ptid:option.ptid };
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						let list = res.data['cartList'];
			
						let cartList = list.map(item => {
							return item;
						});
						this.cartList=cartList;
						this.calcTotal(); 
					} else {
						uni.showToast({ title: res.data.msg, icon: 'none' });
					}
				},
				error => {
					console.log(error);
				}
			);
			
		}
		
		if (option.data != undefined) {
			let listdata = JSON.parse(option.data);
			let opts = {
				url: '/productApi/getcartlist/',
				method: 'post'
			};

			let param = listdata;
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						let list = res.data['cartList'];

						let cartList = list.map(item => {
							return item;
						});
						this.cartList = cartList;
						this.calcTotal(); 
					} else {
						uni.showToast({ title: res.data.msg, icon: 'none' });
					}
				},
				error => {
					console.log(error);
				}
			);
		}
	},
	methods: {
		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer);
		},
		numberChange(data) {
			// this.number = data.number;
			this.cartList[data.index].number = data.number;
			this.calcTotal();
		},
		changePayType(type) {
			this.payType = type;
		},
		submit() {
			if(this.cartList.length==0){
				this.$api.msg("产品为空，无法提交");return;
			}
			if(this.addressData==""){
				this.$api.msg("请提交收货地址");return;
			}
			
			uni.showLoading({
			    title: '创建订单中'
			});

			let cartliststr = JSON.stringify(this.cartList);
			let opts = {
				url: '/orderApi/createorder/',
				method: 'post'
			};
			
			let param = {remark:this.desc,cartliststr:cartliststr,addressData:JSON.stringify(this.addressData),total_fee:this.total,freight_fee:this.freight};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						uni.redirectTo({
							url: '/pages/money/pay?oid='+res.data["oid"]
						});
					} else {
						setTimeout(function () {
						    uni.hideLoading();
						}, 2000);
						uni.showToast({ title: res.data.msg, icon: 'none' });
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
			
			
		},
		//计算总价
		calcTotal() {
			let list = this.cartList;
		
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			list.forEach(item => {
					total += item.price * item.number;
			});
			
			this.total = Number(total.toFixed(2));
			
		},
		stopPrevent() {}
	}
};
